import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";
import { useDispatch } from "react-redux";
import { showLoading, hideLoading } from "../redux/features/alertSlice";

const Register = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [form, setForm] = useState({
    name: "",
    email: "",
    password: "",
    role: "student", // Default role is student
    identifier: "", // This will be either student roll number or admin ID
  });
  const [adminkey, setAdminkey] = useState("");

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm({
      ...form,
      [name]: value,
    });
  };

  const handleRoleChange = (e) => {
    setForm({
      ...form,
      role: e.target.value,
      identifier: "", // Reset identifier when role changes
    });
  };

  const handleAdminPasskey = (e) => {
    setAdminkey(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (form.role === "admin" && adminkey !== "nitr2026") {
      toast.error("Admin key provided is wrong ");
      return;
    }

    try {
      dispatch(showLoading());
      const res = await axios.post("/api/v1/user/register", form);
      setTimeout(() => {
        dispatch(hideLoading());
        if (res.data.success) {
          toast.success("User registered successfully");
          navigate("/login");
        } else {
          toast.error(res.data.message);
        }
      }, 1500);
    } catch (error) {
      setTimeout(() => {
        dispatch(hideLoading());
        alert("Some error occurred");
        console.log(error);
      }, 1500);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-900 relative">
      <div
        className="absolute inset-0 bg-cover bg-center z-0"
        style={{ backgroundImage: "url('/bg-nitr.jpg')" }}
      >
        <div className="absolute inset-0 bg-black opacity-60"></div>
      </div>
      <div className="relative z-10 flex flex-col md:flex-row items-center justify-center max-w-6xl mx-auto p-4 md:p-8 lg:p-12 space-y-6 md:space-y-0 md:space-x-6">
        <div className="text-white text-center md:text-left max-w-md space-y-4">
          <h2 className="text-3xl md:text-4xl lg:text-5xl font-bold">
            Welcome to NIT Rourkela's Club Portal
          </h2>
          <p className="text-lg md:text-xl">
            Explore, learn, and grow with our diverse club opportunities.
            Discover the resources you need to prepare for inductions, apply for
            clubs that match your interests, and more.
          </p>
        </div>
        <div className="bg-white p-8 rounded-lg shadow-lg w-full max-w-md">
          <h2 className="text-2xl font-bold mb-6 text-gray-900 text-center">
            Register
          </h2>
          <form onSubmit={handleSubmit}>
            <div className="mb-4">
              <label
                htmlFor="name"
                className="block text-gray-700 font-semibold mb-2"
              >
                Name
              </label>
              <input
                type="text"
                id="name"
                name="name"
                value={form.name}
                onChange={handleChange}
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              />
            </div>
            <div className="mb-4">
              <label
                htmlFor="email"
                className="block text-gray-700 font-semibold mb-2"
              >
                Email
              </label>
              <input
                type="email"
                id="email"
                name="email"
                value={form.email}
                onChange={handleChange}
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              />
            </div>
            <div className="mb-4">
              <label
                htmlFor="password"
                className="block text-gray-700 font-semibold mb-2"
              >
                Password
              </label>
              <input
                type="password"
                id="password"
                name="password"
                value={form.password}
                onChange={handleChange}
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              />
            </div>
            <div className="mb-4">
              <label
                htmlFor="role"
                className="block text-gray-700 font-semibold mb-2"
              >
                Role
              </label>
              <select
                id="role"
                name="role"
                value={form.role}
                onChange={handleRoleChange}
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              >
                <option value="student">Student</option>
                <option value="admin">Admin</option>
              </select>
            </div>
            <div className="mb-4">
              <label
                htmlFor="identifier"
                className="block text-gray-700 font-semibold mb-2"
              >
                {form.role === "student" ? "Student Roll Number" : "Admin ID"}
              </label>
              <input
                type="text"
                id="identifier"
                name="identifier"
                value={form.identifier}
                onChange={handleChange}
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              />
            </div>
            {form.role === "admin" && (
              <div className="mb-4">
                <label
                  htmlFor="adminkey"
                  className="block text-gray-700 font-semibold mb-2"
                >
                  Admin Passkey (Provided by college)
                </label>
                <input
                  type="password"
                  id="adminkey"
                  name="adminkey"
                  value={adminkey}
                  onChange={handleAdminPasskey}
                  className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
                  required
                />
              </div>
            )}
            <div className="mb-4">
              <button
                type="submit"
                className="w-full bg-indigo-600 text-white py-2 rounded-lg font-semibold hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 transition-transform transform hover:scale-105"
              >
                Register
              </button>
            </div>
            <div className="text-center">
              <p className="text-gray-700">
                Already a user?{" "}
                <Link to="/login" className="text-indigo-600 hover:underline">
                  Sign in
                </Link>
              </p>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Register;
